import Main from '../view/mian.vue'
export default [
    {
        path: '/',
        name: '_home',
        redirect: '/home',
        component: Main,
        children: [
            {
                path: '/home',
                name: 'home',
                meta: {
                    title: '首页',
                },
                component: () => import('../view/home/home.vue')
            },
            {
                path: '/login',
                name: 'login',
                meta: {
                    title: '登录',
                },
                component: () => import('../view/sign/login.vue')
            },
            {
                path: '/register',
                name: 'register',
                meta: {
                    title: '注册',
                },
                component: () => import('../view/sign/register.vue')
            },
            {
                path: '/forgetPwd',
                name: 'forgetPwd',
                meta: {
                    title: '忘记密码',
                },
                component: () => import('../view/sign/forget.vue')
            },
            {
                path: '/contract',
                name: 'contract',
                meta: {
                    title: '合约交易',
                },
                component: () => import('../view/trade-contract/contract.vue')
            },
            {
                path: '/contract2',
                name: 'contract2',
                meta: {
                    title: '期权交易',
                },
                component: () => import('../view/trade-contract/qqexchange/contract2.vue')
            },
            {
                path: '/contractOrder',
                name: 'contractOrder',
                meta: {
                    title: '合约记录',
                },
                component: () => import('../view/contractOrder/contractOrder.vue')
            },

            // 交易大厅
            {
                path: '/transactionList',
                name: 'transactionList',
                meta: {
                    title: '交易大厅',
                },
                component: () => import('@/view/frenchCurrency/transactionList/transactionList.vue')

            },
            // 法币-购买
            {
                path: '/buyPage',
                name: 'buyPage',
                meta: {
                    title: '购买',
                },
                component: () => import('@/view/frenchCurrency/buyPage/buyPage.vue')
            },
            // 法币-出售
            {
                path: '/sellPage',
                name: 'sellPage',
                meta: {
                    title: '出售',
                },
                component: () => import('@/view/frenchCurrency/sellPage/sellPage.vue')
            },
            // 发布购买
            {
                path: '/releaseBuy',
                name: 'releaseBuy',
                meta: {
                    title: '发布购买',
                },
                component: () => import('@/view/frenchCurrency/releaseBuy/releaseBuy.vue')
            },
            // 发布出售
            {
                path: '/releaseSell',
                name: 'releaseSell',
                meta: {
                    title: '发布出售',
                },
                component: () => import('@/view/frenchCurrency/releaseSell/releaseSell.vue')
            },
            // 交易明细
            {
                path: '/transactionDetail',
                name: 'transactionDetail',
                meta: {
                    title: '交易明细',
                },
                component: () => import('@/view/frenchCurrency/transactionDetail/transactionDetail.vue')
            },
            // 发布交易首页
            {
                path: '/publishHome',
                name: 'publishHome',
                meta: {
                    title: '发布交易',
                },
                component: () => import('@/view/frenchCurrency/publishHome/publishHome.vue')
            },
            // 公告列表
            {
                path: '/notice',
                name: 'notice',
                meta: {
                    title: '公告列表',
                },
                component: () => import('@/view/notice/notice.vue')
            },
            // 公告详情
            {
                path: '/noticeData',
                name: 'noticeData',
                meta: {
                    title: '公告详情',
                },
                component: () => import('@/view/frenchCurrency/noticeData/noticeData.vue')
            },
            // 帮助中心
            {
                path: '/about',
                name: 'about',
                meta: {
                    title: '帮助中心',
                },
                component: () => import('@/view/frenchCurrency/about/about.vue')
            },
            // 个人中心
            {
                path: '/center',
                name: 'center',
                redirect: '/center/baseInfo',
                meta: {
                    title: '个人中心',
                },
                component: () => import('@/view/center/index.vue'),
                children: [
                    {
                        path: '/center/baseInfo',
                        name: 'baseInfo',
                        meta: {
                            title: '基本信息',
                        },
                        component: () => import('../view/center/children/baseInfo.vue')
                    },
                    {
                        path: '/center/baseInfo/resetPwd',
                        name: 'resetPwd',
                        meta: {
                            title: '修改登录密码',
                        },
                        component: () => import('../view/center/children/resetPwd.vue')
                    },
                    {
                        path: '/center/baseInfo/maneyPwd',
                        name: 'maneyPwd',
                        meta: {
                            title: '设置资金密码',
                        },
                        component: () => import('../view/center/children/maneyPwd.vue')
                    },
                    {
                        path: '/center/baseInfo/bindTel',
                        name: 'bindTel',
                        meta: {
                            title: '绑定手机号',
                        },
                        component: () => import('../view/center/children/bindTel.vue')
                    },
                    {
                        path: '/center/baseInfo/bindEmail',
                        name: 'bindEmail',
                        meta: {
                            title: '绑定邮箱',
                        },
                        component: () => import('../view/center/children/bindEmail.vue')
                    },
                    {
                        path: '/center/identity',
                        name: 'identity',
                        meta: {
                            title: '身份认证',
                        },
                        component: () => import('../view/center/children/identity.vue')
                    },
                    {
                        path: '/center/identity/lowerIdentity',
                        name: 'lowerIdentity',
                        meta: {
                            title: '初级认证',
                        },
                        component: () => import('../view/center/children/lowerIdentity.vue')
                    },
                    {
                        path: '/center/identity/heigherIdentity',
                        name: 'heigherIdentity',
                        meta: {
                            title: '高级认证',
                        },
                        component: () => import('../view/center/children/heigherIdentity.vue')
                    },
                    {
                        path: '/center/baseInfo/gooleVerify',
                        name: 'gooleVerify',
                        meta: {
                            title: '谷歌认证',
                        },
                        component: () => import('../view/center/children/gooleVerify.vue')
                    },
                    {
                        path: '/center/assetInformation',
                        name: 'assetInformation',
                        meta: {
                            title: '资产信息',
                        },
                        component: () => import('../view/center/children/assetInformation.vue')
                    },
                    {
                        path: '/center/paymentMethod',
                        name: 'paymentMethod',
                        meta: {
                            title: '支付方式',
                        },
                        component: () => import('../view/center/children/paymentMethod.vue')
                    },
                    {
                        path: '/center/capitalSubsidiary',
                        name: 'capitalSubsidiary',
                        meta: {
                            title: '资金明细',
                        },
                        component: () => import('../view/center/children/capitalSubsidiary.vue')
                    },
                    {
                        path: '/center/paymentMethod/addPayment',
                        name: 'addPayment',
                        meta: {
                            title: '添加支付方式',
                        },
                        component: () => import('../view/center/children/addPayment.vue')
                    },
                    {
                        path: '/center/assetInformation/topUp',
                        name: 'topUp',
                        meta: {
                            title: '充值',
                        },
                        component: () => import('../view/center/children/topUp.vue')
                    },
                    {
                        path: '/center/invitecode',
                        name: 'invitecode',
                        meta: {
                            title: '我的邀请码',
                        },
                        component: () => import('../view/center/children/invitecode.vue')
                    },
                    {
                        path: '/center/friend',
                        name: 'friend',
                        meta: {
                            title: '我的好友',
                        },
                        component: () => import('../view/center/children/friend.vue')
                    },
                    {
                        path: '/center/commission',
                        name: 'commission',
                        meta: {
                            title: '推广佣金',
                        },
                        component: () => import('../view/center/children/commission.vue')
                    },
                ]
            }
        ]
    }
]



