//谷歌验证
Public.googleVerify= function (isLogin) { //直接调用 不用额外判断用户谷歌开关状态 内部统一处理
    return new Promise ((resolve, reject)=>{
        if( isLogin || JSON.parse(sessionStorage.getItem('userInfo')).is_start_google == 1 ){ //谷歌验证已开启的用户
            // '请输入谷歌验证码', '谷歌验证'
            vue.$prompt(vue.$t('googleDialog.tips[0]'), vue.$t('googleDialog.tips[1]'), {
                confirmButtonText: vue.$t('center.baseInfo[33]'),
                cancelButtonText:  vue.$t('center.baseInfo[34]'),
                customClass: 'prompt_googleVerify',
                inputType:'password',
                inputPattern: /^\d{6}$/,
                inputErrorMessage: vue.$t('googleDialog.tips[2]'),
            }).then((data) => {
                // 请求谷歌验证码接口，成功返回true 失败false
                let _data = {
                    dyGoodleCommand: data.value
                }
                checkGoogleCode(_data).then((res)=>{
                    if(res.data.status==200){
                        resolve(true)
                    } else{
                        resolve(false)
                    }
                })
            }).catch(() => {
                reject('取消输入')
            });
        } else{ //谷歌验证关闭的用户
            resolve(true)
        }
        
    })

};
// 保留位数
Public.savePoint = function (code) {
    var obj ={
        'btc_usdt': 2,
        'eth_usdt': 2,
        'bch_usdt': 2,
        'etc_usdt': 4,
        'xrp_usdt': 5,
        'ltc_usdt': 2,
        'eos_usdt': 4,
        'dash_usdt': 2,
        'ht_usdt': 4,
        'trx_usdt': 6,
        'imc_usdt': 4,
        'defalut': 2
    }
    var num = obj[code]? obj[code] : obj.defalut
    return num
}
Public.transPrice = function (val, code) {//按需将价格位数保留
    if(val == undefined) return 0 // 
    var num = this.savePoint(code)
    var _num = Number(val).toFixed(num + 1);
    return _num.substr(0, _num.length - 1)
};

Public.CryptographicLand = function (argument) {//登陆密码加密
    return md5(argument) ;
};

Public.Cryptographiccapital = function (argument) {//资金密码加密
    return md5(argument);
}
export default Public


