<template>
    <div id="app" :class="theme=='day'?'mainDay' :'mainNight' ">
        <!-- <img src="./assets/logo.png"/> -->
        <router-view/>
    </div>
</template>
<script>
export default {
  name: 'App',
  data(){
    return {
      theme:'day'
    }
  },
  created(){
    this.theme =  this.$cookies.get('theme') || 'day'
    this.$bus.$on('changeTheme', (val)=>{
      this.theme = val
    })
  },
  beforeDestroy() {
    this.$bus.$off('changeTheme')
  }
}
</script>
<style lang="less">
     @import './assets/reset.less';
     @import './assets/index.less';
</style>



