<style lang="less">
    @import 'index.less';

    #main{
        overflow: hidden;
    }
</style>
<template>
    <div id="main" class="main" :style="`min-width:${minWidth}`">
        <Header ref="header"></Header>
        <router-view />
        <Footer></Footer>
    </div>
</template>
<script>
    import Header from "../components/header/index.vue"
    import Footer from "../components/footer/index.vue"
    export default {
        data() {
            return {
            }
        },
        mounted() {

        },
        computed: {
            minWidth() {
                if (this.$route.path == '/contract') return '1480px'
                else return '1320px'
            }

        },
        created() {
            let _this = this;
            if (_this.$cookies.get("language")) {
                _this.$i18n.locale = _this.$cookies.get("language");
                _this.language = _this.$cookies.get("language");
            } else {
                _this.$cookies.set("language", "Chinese", 60 * 60 * 24 * 30);
                _this.language = _this.$cookies.get("language");
            }
        },
        components: {
            Header,
            Footer
        }

    }
</script>


