import Vue from 'vue'
import Router from 'vue-router'
import routes from './router'


Vue.use(Router)
const router = new Router({
    routes,
    // mode: 'history'
})

router.beforeEach((to, from, next) => {
    var vue = new Vue()
    
    // const token = getToken()
    //路由
    next();

})
router.afterEach(to => {
	// console.log(to);
	window.document.title = to.meta.title
    window.scrollTo(0, 0)
})
export default router



