<!--  -->
<template>
    <div class="header-wrapper" :class="dynamicClass">
        <div class="header">
            <!-- 左侧 -->
            <div class="header-left">
                <!-- logo -->
                <router-link class="logoImg" to="/home">
                    <img src="../../assets/img/logo.png" ></router-link>
                <!-- 导航列表 -->
                <ul class="nav">
                    <li v-for="(item, index) in navList">
                        <a v-if="item.isShow" @click="go(item.path)" :class="$route.path == item.path? 'active' : ''">
                            {{item.name}}
                        </a>
                    </li>
                </ul>
                <!-- 公告 -->
                <div class="notice-slider-wrap">
                    <div class="btn-notice" @click="$router.push({path:'/notice'})">{{$t('header.Other[0]')}}</div>
                    <div class="notice-slider">
                        <div class="swiper-wrapper">
                            <router-link :to="{path:'/noticeData', query:{id:item.id}}"
                                v-for="(item, index) in noticeData" :key="index" class="swiper-slide">{{item.title}}
                            </router-link>
                        </div>
                    </div>
                </div>

            </div>
            <!-- 右侧 -->
            <div class="header-right">
                <!-- 已登录 -->
                <div v-if="isLogin" class="header-right-login">
                    <!-- 订单 -->
                    <a v-if="orderUrl" @click="$router.push({name:orderUrl})"><img src="../../assets/img/home/order.png"
                            alt=""> {{$t('header.Other[1]')}}</a>
                    <el-dropdown @command="logout">
                        <span class="el-dropdown-link">
                            <a style="color:#fff" @click="$router.push({name:'baseInfo'})"><img
                                    src="../../assets/img/home/user.png" alt="">{{tel}}</a>
                            <!-- 下拉菜单<i class="el-icon-arrow-down el-icon--right"></i> -->
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item>{{$t('header.Other[2]')}}</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>

                </div>
                <!-- 未登录 -->
                <div v-else class="header-right-noLogin">
                    <a @click="$router.push({name:'login'})"><img src="../../assets/img/home/user.png" alt="">
                        {{$t('header.Other[3]')}}</a>
                </div>
                <!-- 切换黑白 -->
                <!-- <div class="theme">
                    <span @click="changeTheme('night')">黑夜</span>
                    <span @click="changeTheme('day')">白天</span>
                </div> -->

                <div class="language">
                        <el-select class="el-select-language" :value="language"  @change="ChangeLanguage" placeholder="语言">
                            <el-option label="简体中文" value="Chinese"></el-option>
                            <el-option label="中繁体" value="TW"></el-option>
                            <el-option label="Engilish" value="English"></el-option>
                            <el-option label="日本語" value="Japanese"></el-option>
                            <el-option label="한국어" value="Korean"></el-option>
                        </el-select>
                    <!-- <el-dropdown @command="ChangeLanguage"> -->
                        <!-- <span class="el-dropdown-link">
                            <img v-if="this.$cookies.get('language') =='Chinese'" src="../../assets/img/home/China-icon.png" alt="">
                            <img v-else src="../../assets/img/home/USA.png" alt="">

                            <i class="el-icon-arrow-down el-icon--right"></i>
                        </span> -->
                        <!-- <span class="el-dropdown-link">{{language}}</span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="Chinese">
                                <div>
                                    简体中文
                                </div>
                            </el-dropdown-item>
                            <el-dropdown-item command="TW">
                                <div>
                                    中繁体
                                </div>
                            </el-dropdown-item>
                            <el-dropdown-item command="English">
                                <div>
                                    Engilish
                                </div>
                            </el-dropdown-item>
                            <el-dropdown-item command="Japanese">
                                <div>
                                    日本語
                                </div>
                            </el-dropdown-item>
                            <el-dropdown-item command="Korean">
                                <div>
                                    한국어
                                </div>
                            </el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown> -->
                </div>


            </div>


        </div>

    </div>
</template>

<script>
    import Swiper from "swiper"
    import { zixun } from "@/api/public"

    export default {
        data() {
            return {
                language:'',
                noticeData: "",

                // 登录状态
                isLogin: false,
                tel: ''
            };
        },
        components: {},
        computed: {
            navList() {
                var arr = [                    //首页
                    { name: this.$t('header.navList[0]'), path: '/home', isShow: true, selfClass: "hot" }, // selfClass可用来加hot图标等
                    //合约交易
                    { name: this.$t('header.navList[1]'), path: '/contract', isShow: true, selfClass: "" },
                    //期权交易
                    { name: this.$t('header.navList[5]'), path: '/contract2', isShow: true, selfClass: "" },
                    // //法币交易
                    // { name: this.$t('header.navList[2]'), path: '/transactionList', isShow: true, selfClass: "" },
                    // //发布交易
                    // { name: this.$t('header.navList[3]'), path: '/publishHome', isShow: true, selfClass: "" },
                    //关于我们
                    { name: this.$t('header.navList[4]'), path: '/about', isShow: true, selfClass: "" }
                ]
                return arr
            },
            // 头部背景透明
            dynamicClass() {
                var tansparent = ['/login', '/register', '/forgetPwd']
                if (this.$route.path == '/home') return 'inHome'
                if (tansparent.includes(this.$route.path)) return "tansparent"
                else return ''
            },
            // 订单显示+跳转 
            orderUrl() {
                // 当前path等于对象的某个value时，跳转name为key的路由
                var obj = {
                    'contractOrder': '/contract',
                    'transactionDetail': '/transactionList',
                }
                for (var i in obj) {
                    if (obj[i] == this.$route.path) {
                        return i
                    }
                    if (obj[i] == undefined) {
                        return ""
                    }
                }
            },
        },
        created() {
            this.setLanguage();
            this.getNotice()
            // 已经登录
            if (sessionStorage.token && sessionStorage.account) {
                this.isLogin = true
                this.tel = this.$public.phoneEncryption(sessionStorage.getItem('mobile'))
            } else { this.isLogin = false }
            // 监听登录完成 from  login.vue
            this.$bus.$on('loginCompleted', () => {
                this.isLogin = true
                this.tel = this.$public.phoneEncryption(sessionStorage.getItem('mobile'))
            })
        },
        methods: {
            // 改变主题
            changeTheme(val) {
                this.$cookies.set('theme',val, 60 * 60 * 24 * 30)
                this.$bus.$emit('changeTheme', val)
            },  
            // 跳转
            go(path) {
                if (path == '/transactionList') {
                    if (!sessionStorage.token) {
                        this.$public.msg(this.$t('contract.tips[0]'), 'warning', this)
                        return
                    }
                }
                this.$router.push({ path })
            },
            // 登出
            logout() {
                sessionStorage.clear()
                this.isLogin = false;
                this.$router.push({ name: 'login' })
            },
            // 获取公告
            getNotice() {
                zixun({ size: 3 }).then((r) => {
                    if (r.data.status == 200) {
                        this.noticeData = r.data.data.res
                        this.$nextTick(() => {
                            this.swiper = new Swiper('.notice-slider', {
                                loop: true, // 循环模式选项
                                noSwiping: true,
                                observer: true,
                                direction: "vertical",
                                autoplay: { 
                                    delay: 2000
                                },
                            })
                        })
                    }
                })
            },
            ChangeLanguage(val) {
                //中英文切换
                this.$cookies.set("language", val, 60 * 60 * 24 * 30);
                this.setLanguage();
                location.reload();
            },
            setLanguage() {
                const val = this.$cookies.get("language");
                this.$i18n.locale = val;
                // const config = {
                //     Chinese:'简体中文',
                //     TW:'中繁体',
                //     English:'Engilish',
                //     Japanese:'日本語',
                //     Korean:'한국어'
                // };
                // const index = Object.keys(config).indexOf(val);
                // this.language =  Object.values(config)[index];
                this.language = val;
            }

        },
        beforeDestroy() {
            this.$bus.$off('loginCompleted')
        }
    }
</script>
<style lang=less>
    @import "./index.less";
    @import "../../assets/index.less";
    .el-select.el-select-language {
        width: 110px;
        input {
            background: transparent;
            color:#fff;
            height: 30px;
            line-height: 30px;
        }
        .el-input__icon {
            line-height: 30px;
        }
    }
</style>


