<template>
    <div class="footer" :class="{ 'change-bgcolor': changeBgColor,'hidden-footer': hiddenFooter}">
        <div class="footer-c">
            <div class="footer-line clr" :class="{ 'change-line': changeBgColor}">
                <ul class="footer-item">
                    <p class="logo-name">
                        BITPUI
                    </p>
                    <div class="img-list">
                        <img src="../../assets/img/footerImg/footer_1.png" alt="" class="list-img" />
                        <img src="../../assets/img/footerImg/footer_2.png" alt="" class="list-img" />
                        <img src="../../assets/img/footerImg/footer_3.png" alt="" class="list-img" />
                    </div>
                </ul>
                <ul class="footer-item">
                    <li class="footer-list">
                        <!-- 条款协议 -->
                        {{$t('footer.clause[0]')}}
                    </li>
                    <li @click="$router.push({ name: 'about', query: { id: 4 } })"  class="footer-list">
                        <!-- 关于反洗钱 -->
                        {{$t('footer.clause[1]')}}
                    </li>
                    <li @click="$router.push({ name: 'about', query: { id: 1 } })"  class="footer-list">
                        <!-- 隐私条款 -->
                        {{$t('footer.clause[2]')}}
                    </li>
                    <li @click="$router.push({ name: 'about', query: { id: 0 } })"  class="footer-list">
                        <!-- 用户协议 -->
                        {{$t('footer.clause[3]')}}
                    </li>
                </ul>
                <ul class="footer-item">
                    <li class="footer-list">
                        <!-- 工具支持 -->
                        {{$t('footer.tool[0]')}}
                    </li>
                    <li @click="$router.push({ name: 'about', query: { id: 5 } })"  class="footer-list">
                        <!-- 新手指南 -->
                        {{$t('footer.tool[1]')}}
                    </li>
                    <li @click="$router.push({ name: 'about', query: { id: 0 } })"  class="footer-list">
                        <!-- 帮助中心 -->
                        {{$t('footer.tool[2]')}}
                    </li>
                </ul>
                <ul class="footer-item">
                    <li class="footer-list">
                        <!-- 联系我们 -->
                        {{$t('footer.contact[0]')}}
                    </li>
                    <!-- <li class="footer-list"> -->
                        <!-- 微信客服 -->
                        <!-- {{$t('footer.contact[1]')}}
                    </li> -->
                    <li class="footer-list">
                        BITPUI@163.com
                    </li>
                </ul>
            </div>
            <div class="fter">
                <h2>{{$t('footer.fter[0]')}}</h2>
                <p>&copy;2017 - 2019 {{$t('footer.fter[1]')}}</p>
            </div>
        </div>
    </div>
</template>
<script>
import { appLink } from "@/api/public";
export default {
  data() {
    return {
      appLink
    };
  },
  computed: {
    goAbout() {},
    // 首页footer样式
    changeBgColor() {
      var changeColor = ["/home"];
      if (changeColor.includes(this.$route.path)) {
        return true;
      } else {
        return false;
      }
    },
    // 合约页隐藏footer
    hiddenFooter() {
      var hiddenFoo = ["/contract", '/contract2'];
      if (hiddenFoo.includes(this.$route.path)) {
        return true;
      } else {
        return false;
      }
    }
  }
};
</script>
<style lang="less">
@import "index.less";
@import "../../assets/index.less";

</style>


