import Vue from 'vue'
import VueI18n from 'vue-i18n';

import zhlocale from './lang/Chinese.js'
import enlocale from './lang/English.js'
import TW from './lang/TW.js'
import Japanese from './lang/Japanese.js'
import Korean from './lang/Korean.js'

import enLocale from 'element-ui/lib/locale/lang/en'
import zhLocale from 'element-ui/lib/locale/lang/zh-CN'

Vue.use(VueI18n)

const i18n = new VueI18n({
    locale: "Chinese",    // 语言标识
    messages: {
        'Chinese':Object.assign(zhlocale,zhLocale),
        'English':Object.assign(enlocale,enLocale),
        'TW': TW,
        'Japanese':Japanese,
        'Korean':Korean
    },
});

export default  i18n


